<template>
  <img src="./assets/gptvison.png">
  <h4>Um descritor de imagem empoderado pelo ChatGPT</h4>
  <p></p>
  <p></p>
  <div class="input-group mb-3">     
    <input class="form-control" v-model="imageUrl" placeholder="Image URL">
    <button class="btn btn-primary" @click="describeImage()">Describe</button>
  </div>
  <p id="status"></p>
  <p></p>  
  <p></p>
  <img v-bind:src= "imageUrl" height="300" width="400" alt="Imagem">
  <p align="center">{{ imageDescription }}</p>
  <p></p>
  
  <div class="input-group mb-3">     
    <input class="form-control w-50" v-model="question" placeholder="Faça uma pergunta sobre o conteúdo da imagem">
    <button class="btn btn-primary" @click="Questao()">Send</button>
  </div>
  
</template>


<script>
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap/dist/js/bootstrap.js'


export default {
  name: 'App',
  data() {
  return {   
    imageUrl: 'https://cdn-icons-png.flaticon.com/512/4347/4347523.png',
    imageDescription: '',
    question: ''
  }
 },
  methods: {  

      async describeImage() {
       console.log(this.imageUrl); 
        document.getElementById('status').innerHTML="AGUARDE A DESCRIÇÂO!"  
        const chaveAPI = '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';
        
          try {
            const resposta = await axios.post(
              'https://api.openai.com/v1/completions',
              {
                model: 'text-davinci-003',
                prompt: `Estou enviado um link da imagem para você descrever em poucas palavras o conteúdo da imagem. Segue o link da imagem: ${this.imageUrl}`,
                max_tokens: 2825,
                user: "1",
                stop: ["#", ";"],
                temperature: 0.90,
                presence_penalty: 0.0,
                frequency_penalty: 0.0
              },
              {
                headers: {
                  'Authorization': `Bearer ${chaveAPI}`,
                  'Content-Type': 'application/json'
                }
              }
            );

            if (resposta.status === 200) {
                const respostaAPI = resposta.data.choices[0].text.trim();
                this.resposta = respostaAPI;
                this.imageDescription = respostaAPI;
                document.getElementById('status').innerHTML=""                         

                const synth = window.speechSynthesis;
                const utterance = new SpeechSynthesisUtterance(this.resposta);
                utterance.voice = synth.getVoices().find(voice => voice.name === "Microsoft Daniel - Portuguese (Brazil)");
                synth.speak(utterance);
                console.log(synth.getVoices());            

                } else {
                console.log('Erro ao acessar a API do OpenAI');
                console.log(resposta);
                }

          } catch (erro) {
            console.error('Erro ao fazer a solicitação para a API do OpenAI', erro);      
        }
    },

    async Questao() {
       console.log(this.imageUrl); 
        document.getElementById('status').innerHTML="AGUARDE A RESPOSTA!"  
        const chaveAPI = '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';
        
          try {
            const resposta = await axios.post(
              'https://api.openai.com/v1/completions',
              {
                model: 'text-davinci-003',
                prompt: this.question+':'+this.imageUrl,
                max_tokens: 2825,
                user: "1",
                stop: ["#", ";"],
                temperature: 0.90,
                presence_penalty: 0.0,
                frequency_penalty: 0.0
              },
              {
                headers: {
                  'Authorization': `Bearer ${chaveAPI}`,
                  'Content-Type': 'application/json'
                }
              }
            );

            if (resposta.status === 200) {
                const respostaAPI = resposta.data.choices[0].text.trim();
                this.resposta = respostaAPI;
                this.imageDescription = respostaAPI;
                document.getElementById('status').innerHTML=""                         

                const synth = window.speechSynthesis;
                const utterance = new SpeechSynthesisUtterance(this.resposta);
                utterance.voice = synth.getVoices().find(voice => voice.name === "Microsoft Maria - Portuguese (Brazil)");
                synth.speak(utterance);
                console.log(synth.getVoices());            

                } else {
                console.log('Erro ao acessar a API do OpenAI');
                console.log(resposta);
                }

          } catch (erro) {
            console.error('Erro ao fazer a solicitação para a API do OpenAI', erro);      
        }
    }




  }
}


</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
